import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
  IOrganizationModel,
  IOrganizationRequest,
} from "app/org-management-setup/models/organizations";
import { OrganizationsService } from "app/org-management-setup/services/organizations.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-organizations",
  templateUrl: "./organizations.component.html",
  styleUrls: ["./organizations.component.scss"],
})
export class OrganizationsComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "en_Name",
    "ar_Name",
    "code",
    "actions",
  ];
  optionalColomns: string[] = ["logo"];
  dataSource: MatTableDataSource<IOrganizationModel>;
  removeFiles: boolean = false;
  isLoaded: boolean = false;
  constructor(
    private organizationsService: OrganizationsService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<OrganizationsComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      ar_name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_name: [null, [Validators.required, CustomValidator.whiteSpace]],
      code: [null, [Validators.required, CustomValidator.whiteSpace]],
      logo: [null /* , [Validators.required] */],
    });
		if (this.data.value) {
			this.formCtrls.ar_name.setValue(this.data.value);
			this.formCtrls.en_name.setValue(this.data.value);
		}
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId() {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.organizationsService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IOrganizationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    this.submitted = true;
    this.removeFiles = false;
    if (this.form.valid) {
      const model: IOrganizationRequest = {
        ID: this.form.value.id,
        Ar_Name: this.form.value.ar_name,
        En_Name: this.form.value.en_name,
        code: this.form.value.code,
        logo: this.form.value.logo,
      };

      model.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IOrganizationRequest) {
    this.showLoading();
    this.organizationsService
      .create(model)
      .subscribe((res: IResponse<IOrganizationModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.lastPage, this.page.pageSize);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IOrganizationRequest) {
    this.showLoading();
    this.organizationsService
      .update(model)
      .subscribe((res: IResponse<IOrganizationModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.currentPage, this.page.pageSize); // current page after update
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  organizationToEdit(organization: IOrganizationModel) {
    this.form.patchValue(organization);
    this.formCtrls.ar_name.setValue(organization.ar_Name);
    this.formCtrls.en_name.setValue(organization.en_Name);
    // console.log(this.form.value);
  }

  onDelete(organization: IOrganizationModel): void {
    let msg = this.msgTranslate(organization.ar_Name, organization.en_Name);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(organization);
    });
  }

  delete(organization: IOrganizationModel) {
    this.showLoading();
    this.organizationsService
      .Delete(organization.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.pageSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.pageSize);
          }
          if (organization.id == this.formCtrls.id.value) this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
    this.removeFiles = true;
  }

	onCloseDialog() {
		this.dialogRef.close();
	}
}
